import React from 'react';
import styled, { css } from 'styled-components';
import { transition } from 'style/const/transition';
import { focusBorderMixin } from 'style/mixin/focus';
import { ButtonText1 } from 'components/Text/Button';
import { color } from 'style/const/color';
import { icon } from 'style/const/icon';

const containerBorderRadiusPx = 24;

interface StyledHeaderButtonProps {
    isVisible?: boolean;
}

const StyledHeaderButton = styled.div<StyledHeaderButtonProps>`
    position: relative;

    display: flex;
    align-items: center;

    margin-left: 24px;

    box-sizing: border-box;
    height: 80px;
    padding: 16px 36px;
    border-radius: ${containerBorderRadiusPx}px;

    transition: background-color ${transition.default};
    will-change: background-color;

    ${focusBorderMixin({ borderRadius: containerBorderRadiusPx })}

    &:focus {
        background: ${color.button.secondary1};
    }

    &:first-of-type {
        margin-left: 0;
    }

    ${({ isVisible }) =>
        !isVisible &&
        css`
            display: none;
        `}
`;

const textHeightPx = icon.m.sizePx;

const StyledText = styled(ButtonText1)`
    margin-left: 8px;

    height: ${textHeightPx}px;
    padding: 0 4px;

    line-height: ${textHeightPx}px;

    color: ${color.text.white};

    opacity: 0.88;
`;

interface HeaderButtonProps {
    text: string;
    isVisible?: boolean;
    onClick?: () => void;
}

export const HeaderButton: React.FC<HeaderButtonProps> = ({ text, isVisible, onClick: handleClick, children }) => {
    return (
        <StyledHeaderButton tabIndex={0} onClick={handleClick} isVisible={isVisible}>
            {children}
            <StyledText>{text}</StyledText>
        </StyledHeaderButton>
    );
};
