import { splitByThousands } from 'tools/split-by-thousands';
import { AppState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';

const routeToTitleMap: Record<string, string> = {
    [ROUTES.CATALOG]: 'Самокат',
    [ROUTES.CART]: 'Корзина',
    [ROUTES.STATUS]: 'Самокат',
    [ROUTES.ERROR]: 'Самокат',
    [ROUTES.NO_AREA]: 'Адрес доставки',
    [ROUTES.ADDRESS_FULL_ALL]: 'Адрес доставки и телефон',
};

const getTitle = (state: AppState) => routeToTitleMap[selectPathname(state)] || '';

const getAddress = (state: AppState) => ({
    text: state.userData.address,
    isVisible: selectPathname(state) === ROUTES.CATALOG,
});

const getCart = (state: AppState) => ({
    isVisible: state.cart.totalCount > 0 && selectPathname(state) === ROUTES.CATALOG,
    count: state.cart.totalCount,
    price: splitByThousands(Math.ceil(state.cart.totalPrice / 100)),
});

const routeToVisibilityMap: Record<string, boolean> = {
    [ROUTES.CATALOG]: true,
    [ROUTES.CART]: true,
    [ROUTES.STATUS]: true,
    [ROUTES.ERROR]: true,
    [ROUTES.NO_AREA]: true,
    [ROUTES.ADDRESS_SIMPLE]: true,
};

const isVisible = ({ router }: AppState) => routeToVisibilityMap[router.location.pathname] || false;

export const headerView: AppReducer = (state) => {
    const { header } = state.layout;

    header.title = getTitle(state);
    header.address = getAddress(state);
    header.cart = getCart(state);
    header.isVisible = isVisible(state);
    header.isBackButtonVisible = Boolean(window.history.length);
};
