import React from 'react';
import styled from 'styled-components';
import { color } from 'style/const/color';
import { FootnoteText1 } from 'components/Text/Footnote';

export type LabelType = 'sold-out' | 'few-left' | 'sale' | 'price-changed';

const contentMap: Record<LabelType, string> = {
    'sold-out': 'Раскупили',
    'few-left': 'Осталось мало',
    'price-changed': 'Изменилась цена',
    sale: 'Скидка',
};

const textColorMap: Record<LabelType, string> = {
    'sold-out': color.text.warning,
    'few-left': color.text.warning,
    'price-changed': color.text.warning,
    sale: color.text.active,
};

interface StyledLabelProps {
    labelType: LabelType;
}
const StyledLabel = styled(FootnoteText1)<StyledLabelProps>`
    color: ${({ labelType }) => textColorMap[labelType]};
`;

export interface LabelProps extends React.HTMLAttributes<HTMLDivElement> {
    labelType: LabelType;
}

export const Label: React.FC<LabelProps> = ({ labelType, children, ...props }) => {
    return (
        <StyledLabel labelType={labelType} {...props}>
            {contentMap[labelType]} {children}
        </StyledLabel>
    );
};
