import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { layout } from 'style/const/layout';

import { CatalogListItem, cardHeightPx } from '../ListItem/CatalogListItem';

const preventScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollLeft = 0;
};

const StyledCatalogList = styled.div`
    position: relative;

    height: ${layout.contentHeightPx}px;

    display: flex;
    flex-direction: column;
    justify-content: center;

    overflow: hidden;
    --spatial-navigation-contain: contain;
`;

export const cardGapPx = 32;

const StyledWrapper = styled.div`
    box-sizing: border-box;
    width: max-content;
    height: ${cardHeightPx}px;
    min-width: 100%;

    display: grid;
    grid-template-columns: repeat(auto-fill, 392px);
    grid-auto-flow: row;
    grid-gap: ${cardGapPx}px;

    will-change: transform;

    transition: transform 0.4s ease-in-out;
`;

export const CatalogList: React.FC = () => {
    const order = useSelector(({ catalogPage }) => catalogPage.order);
    const wrapperXShift = useSelector(({ catalogPage }) => catalogPage.wrapperXShift);

    const wrapperStyle = React.useMemo(
        () => ({
            transform: `translateX(${-1 * wrapperXShift}px)`,
        }),
        [wrapperXShift],
    );

    return (
        <StyledCatalogList onScroll={preventScroll}>
            <StyledWrapper style={wrapperStyle}>
                {order.map((itemId) => (
                    <CatalogListItem key={itemId} id={itemId} />
                ))}
            </StyledWrapper>
        </StyledCatalogList>
    );
};
