import { produce } from 'immer';
import { History } from 'history';
import { mobileDetect } from 'tools/browser';

import { AppReducer, initialState, AppBaseAction } from '../constants';

import { productsData } from './data/products';
import { userData } from './data/user';
import { getRouterModel } from './model/router';
import { cartModel } from './model/cart';
import { userModel } from './model/user';
import { catalogPageView } from './view/catalogPage';
import { catalogPageTouchView } from './view/catalogPage@touch';
import { cartPageView } from './view/cartPage';
import { addressSimplePageView } from './view/addressSimplePage';
import { addressFullPageView } from './view/addressFullPage';
import { headerView } from './view/header';
import { layoutView } from './view/layout';
import { errorPageView } from './view/errorPage';

export const getRootReducer = (history: History) => {
    const routerModel = getRouterModel(history);
    const isMobile = mobileDetect();

    return (state = initialState, action: AppBaseAction) =>
        produce(state, (draft) => {
            const applyReducer = (reducer: AppReducer) => reducer(draft, action);

            [
                // уровень данных
                productsData,
                userData,
            ].forEach(applyReducer);

            [
                // уровень бизнес логики
                routerModel,
                cartModel,
                userModel,
            ].forEach(applyReducer);

            // уровень пропсов для для отрисовки компонентов
            if (isMobile) {
                [
                    // мобильная версия
                    catalogPageTouchView,
                    headerView,
                ].forEach(applyReducer);
            } else {
                [
                    // десктопная версия
                    catalogPageView,
                    cartPageView,
                    addressSimplePageView,
                    addressFullPageView,
                    errorPageView,
                    headerView,
                    layoutView,
                ].forEach(applyReducer);
            }
        });
};
