import { AppState } from '../constants';

export const selectProductById = (state: AppState, itemId: string) => state.products.itemsMap[itemId];
export const selectCartItemById = (state: AppState, itemId: string) => state.cart.itemsMap[itemId];
export const selectPathname = (state: AppState) => state.router.location.pathname;
export const selectHeader = (state: AppState) => state.layout.header;
export const selectOverlay = (state: AppState) => state.layout.overlay;
