import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { askForStockAction } from 'store/actions/server';
import { SHOW_ITEMS_MOCK } from 'mocks';

import { CatalogList } from './List/CatalogList';
import { CatalogSkeleton } from './Skeleton/CatalogSkeleton';

export const Catalog: React.FC = () => {
    const dispatch = useDispatch();
    const order = useSelector(({ catalogPage }) => catalogPage.order);

    React.useEffect(() => {
        dispatch(askForStockAction());
    }, [dispatch]);

    React.useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            setTimeout(() => {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'SHOW_ITEMS',
                        stock: SHOW_ITEMS_MOCK,
                    },
                });

                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'ADD_ITEM',
                        item: '21ff407a-fa98-11e8-80c5-0cc47a817925',
                        count: '2',
                    },
                });
            }, 500);

            setTimeout(() => {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'ADD_ITEM',
                        item: '21ff407c-fa98-11e8-80c5-0cc47a817925',
                        count: '2',
                    },
                });
            }, 1000);

            setTimeout(() => {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'REMOVE_ALL',
                    },
                });
            }, 2000);
        }
    }, []);

    if (order.length) {
        return <CatalogList />;
    }

    return <CatalogSkeleton />;
};
