import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { useThrottledFn } from 'hooks/useThrottledFn';
import { ROUTES } from 'store/constants/router';
import { makePurchaseAction } from 'store/actions/server';
import { Button } from 'components/Button/Button';
import { Pin } from 'components/Pin/Pin';
import { ParagraphText2, ParagraphText1 } from 'components/Text/Paragraph';
import { CaptionText } from 'components/Text/Caption';
import { HeadlineText2 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';

const StyledCartInfo = styled.div`
    position: relative;

    width: 744px;
    padding-top: 48px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledTime = styled(ParagraphText2)`
    color: ${color.text.primary};
`;

const StyledUnit = styled(CaptionText)`
    margin-top: -10px;

    color: ${color.text.primary};
`;

const StyledAddress = styled(HeadlineText2)`
    margin-top: 48px;

    display: flex;
    flex-direction: column;
    align-items: center;

    -webkit-line-clamp: 2;

    color: ${color.text.primary};
`;

const StyledPersonal = styled.div`
    margin-top: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledPersonalLocation = styled(BodyText1)`
    -webkit-line-clamp: 2;

    color: ${color.text.primary};
`;

const StyledPersonalPhoneNumber = styled(HeadlineText2)`
    margin-top: 60px;

    color: ${color.text.primary};
`;

const StyledPersonalName = styled(BodyText1)`
    margin-top: 8px;

    color: ${color.text.primary};
`;

const StyledAskPersonal = styled(ParagraphText1)`
    margin-top: 14px;

    text-align: center;

    color: ${color.text.secondary};
`;

const CartInfoButton = styled(Button)`
    margin-top: 60px;

    &[disabled] {
        opacity: 0.3;
    }
`;

const throttleTimeoutMs = 200;

export const CartInfo: React.FC = () => {
    const dispatch = useDispatch();
    const { address, customerName } = useSelector(({ userData }) => userData);
    const { deliveryTime, makePurchaseButton, hasRequiredData, phoneNumber, personalAddress } = useSelector(
        ({ cartPage }) => cartPage,
    );
    const handlePurchaseClick = useThrottledFn(() => dispatch(makePurchaseAction()), throttleTimeoutMs, undefined, [
        dispatch,
    ]);

    const handleSetAddressClick = useThrottledFn(
        () => dispatch(push(ROUTES.ADDRESS_FULL_ALL)),
        throttleTimeoutMs,
        undefined,
        [dispatch],
    );

    return (
        <StyledCartInfo>
            <Pin>
                <StyledTime>{deliveryTime}</StyledTime>
                <StyledUnit>мин</StyledUnit>
            </Pin>
            <StyledAddress>{address}</StyledAddress>
            {hasRequiredData ? (
                <>
                    <StyledPersonal>
                        <StyledPersonalLocation>{personalAddress}</StyledPersonalLocation>
                        <StyledPersonalPhoneNumber>{phoneNumber}</StyledPersonalPhoneNumber>
                        <StyledPersonalName>{customerName}</StyledPersonalName>
                    </StyledPersonal>
                    <CartInfoButton {...makePurchaseButton} shouldFocusOnMount onClick={handlePurchaseClick}>
                        Перейти к оплате
                    </CartInfoButton>
                </>
            ) : (
                <>
                    <StyledAskPersonal>
                        Чтобы сделать заказ, укажите квартиру, подъезд и другую дополнительную информацию
                    </StyledAskPersonal>
                    <CartInfoButton shouldFocusOnMount onClick={handleSetAddressClick}>
                        Дополнить адрес
                    </CartInfoButton>
                </>
            )}
        </StyledCartInfo>
    );
};
