import { AppState } from 'store/constants';
import { selectCartItemById } from 'store/selectors';

export const preparePrice = (price: number) => Math.ceil(price / 100);

export const prepareCount = (state: AppState, itemId: string) => {
    const cartItem = selectCartItemById(state, itemId);

    return cartItem ? cartItem.count : 0;
};

export const calculateButtonDisabled = ({ cart, products }: AppState, itemId: string) => {
    const productItem = products.itemsMap[itemId];
    const cartItem = cart.itemsMap[itemId];

    return cartItem && cartItem.count === productItem.quantityLeft;
};
