import React from 'react';
import { push } from 'connected-react-router';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { useThrottledFn } from 'hooks/useThrottledFn';
import { selectHeader } from 'store/selectors';
import { goToCartAction } from 'store/actions/server';
import { ROUTES } from 'store/constants/router';
import { HeaderButton } from 'components/HeaderButton/HeaderButton';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';

import cartIcon from './cart.svg';

const cartIconSizePx = icon.m.sizePx;

const StyledIcon = styled.div`
    position: relative;

    width: ${cartIconSizePx}px;
    height: ${cartIconSizePx}px;

    background-image: url('${cartIcon}');
    background-repeat: no-repeat;
    background-size: contain;
`;

const badgeSizePx = 32;
const badgeOffsetPx = cartIconSizePx / 2;

const StyledBadge = styled.span`
    position: absolute;
    left: ${badgeOffsetPx}px;
    bottom: ${badgeOffsetPx}px;

    width: ${badgeSizePx}px;
    height: ${badgeSizePx}px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    line-height: 24px;
    font-size: 20px;
    letter-spacing: -0.019em;

    box-sizing: border-box;

    color: ${color.text.primary};
    background: ${color.button.primary};
`;

const throttleTimeoutMs = 200;

export const HeaderCart: React.FC = () => {
    const dispatch = useDispatch();

    const { count, price, isVisible } = useSelector((state) => selectHeader(state).cart);

    const handleClick = useThrottledFn(
        () => {
            dispatch(push(ROUTES.CART));
            dispatch(goToCartAction());
        },
        throttleTimeoutMs,
        undefined,
        [dispatch],
    );

    const text = `${price} ₽`;

    return (
        <HeaderButton isVisible={isVisible} onClick={handleClick} text={text}>
            <StyledIcon>
                <StyledBadge>{count}</StyledBadge>
            </StyledIcon>
        </HeaderButton>
    );
};
