import React from 'react';
import { Switch, Route } from 'react-router';
import { ROUTES } from 'store/constants/router';

import { AddressFullAll } from './AddressFullAll';
import { AddressFullPhone } from './AddressFullPhone';
import { AddressFullApartment } from './AddressFullApartment';
import { AddressFullEntrance } from './AddressFullEntrance';
import { AddressFullFloor } from './AddressFullFloor';

export const AddressFull = () => (
    <Switch>
        <Route path={ROUTES.ADDRESS_FULL_ALL}>
            <AddressFullAll />
        </Route>
        <Route path={ROUTES.ADDRESS_FULL_PHONE}>
            <AddressFullPhone />
        </Route>
        <Route path={ROUTES.ADDRESS_FULL_APARTMENT}>
            <AddressFullApartment />
        </Route>
        <Route path={ROUTES.ADDRESS_FULL_ENTRANCE}>
            <AddressFullEntrance />
        </Route>
        <Route path={ROUTES.ADDRESS_FULL_FLOOR}>
            <AddressFullFloor />
        </Route>
    </Switch>
);
