import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { SHOW_ITEMS_MOCK } from 'mocks';
// import { GradientFade } from 'components/GradientFade/GradientFade';
import { HeadlineText2 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';

import { CartListItem } from '../ListItem/CartListItem';

const preventScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollTop = 0;
};

const StyledCartList = styled.div`
    position: relative;

    height: 792px;
    width: 832px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledContainerProps {
    topGradientVisible?: boolean;
    bottomGradientVisible?: boolean;
}
const StyledContainer = styled.div<StyledContainerProps>`
    position: relative;

    margin-bottom: 56px;

    max-height: 600px;
    overflow: hidden;

    --spatial-navigation-contain: contain;
`;

const StyledWrapper = styled.div`
    overflow-x: hidden;

    will-change: transform;
    transition: transform 0.5s ease-in-out;
`;

const StyledTotalPrice = styled(HeadlineText2)`
    padding-left: 16px;

    color: ${color.text.primary};
`;

const StyledMinimalPrice = styled(BodyText1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${color.text.warning};
`;

// const gradientSizePx = 160;

export const CartList: React.FC = () => {
    const { order, totalPrice, toMinimumPrice, wrapperYShift } = useSelector(({ cartPage }) => cartPage);

    React.useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            if (!order.length) {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line
                    smart_app_data: {
                        command: 'SHOW_ITEMS',
                        stock: SHOW_ITEMS_MOCK,
                    },
                });

                Object.keys(SHOW_ITEMS_MOCK).forEach((itemId) => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'ADD_ITEM',
                            item: itemId,
                            count: String(Math.max(1, Math.floor(Math.random() * 5))),
                        },
                    });
                });
            }
        }
    }, [order]);

    const wrapperStyle = React.useMemo(
        () => ({
            transform: `translateY(${-1 * wrapperYShift}px)`,
        }),
        [wrapperYShift],
    );

    return (
        <StyledCartList>
            {order.length > 0 && (
                <StyledContainer onScroll={preventScroll}>
                    <StyledWrapper style={wrapperStyle}>
                        {order.map((itemId, index) => (
                            <CartListItem key={itemId} index={index + 1} id={itemId} />
                        ))}
                    </StyledWrapper>
                </StyledContainer>
            )}
            <StyledTotalPrice>Сумма заказа {totalPrice} ₽</StyledTotalPrice>
            {Boolean(toMinimumPrice) && (
                <StyledMinimalPrice>До минимального заказа еще {toMinimumPrice} ₽</StyledMinimalPrice>
            )}
        </StyledCartList>
    );
};
