import { CatalogListItemTouchViewProps } from 'pages/Catalog/ListItem/CatalogListItem@touch';
import { AppState, AppReducer } from 'store/constants';
import { selectProductById } from 'store/selectors';
import { calculateButtonDisabled, prepareCount, preparePrice } from 'store/reducers/helpers/catalogPage';

const prepareBadge = ({ cart, products }: AppState, itemId: string): CatalogListItemTouchViewProps['badge'] => {
    const cartItem = cart.itemsMap[itemId];
    const productItem = products.itemsMap[itemId];

    if (productItem.quantityLeft === 0) {
        return 'sold-out';
    }

    if (cartItem && cartItem.count === productItem.quantityLeft) {
        return 'few-left';
    }

    return undefined;
};

const prepareIndex = ({ catalogPageTouch: { order } }: AppState, itemId: string) => order.indexOf(itemId) + 1;

const prepareCatalogItemsMap = (state: AppState) => {
    const { products } = state;

    return Object.keys(products.itemsMap).reduce<AppState['catalogPageTouch']['itemsMap']>((acc, itemId) => {
        const productItem = selectProductById(state, itemId);

        acc[itemId] = {
            imageUrl: productItem.imageUrl,
            name: productItem.name,
            price: preparePrice(productItem.price),
            count: prepareCount(state, itemId),
            index: prepareIndex(state, itemId),
            badge: prepareBadge(state, itemId),
            buttonDisabled: calculateButtonDisabled(state, itemId),
            disabled: productItem.quantityLeft === 0,
        };

        return acc;
    }, {});
};

export const catalogPageTouchView: AppReducer = (state) => {
    const { products, catalogPageTouch } = state;

    catalogPageTouch.order = Object.keys(products.itemsMap);
    catalogPageTouch.itemsMap = prepareCatalogItemsMap(state);
};
