import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { selectHeader } from 'store/selectors';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';
import { transition } from 'style/const/transition';
import { HeadlineText4 } from 'components/Text/Headline';

import { HeaderAddress } from '../HeaderAddress/HeaderAddress';
import { HeaderCart } from '../HeaderCart/HeaderCart';

import backButtonIcon from './Header.assets/chevron-left.svg';
import logoIcon from './Header.assets/logo.svg';

const headerPaddingTopPx = 32;

interface StyledHeaderProps {
    isVisible: boolean;
}

const StyledHeader = styled.div<StyledHeaderProps>`
    position: relative;

    height: ${layout.headerHeightPx}px;

    box-sizing: border-box;
    padding: ${headerPaddingTopPx}px 128px 0;

    display: flex;
    justify-content: space-between;
    align-items: center;

    --spatial-navigation-contain: contain;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const backButtonTopPx = 24 + headerPaddingTopPx;

const backButtonIconSizePx = icon.m.sizePx;

const StyledBackButton = styled.span`
    position: absolute;
    top: ${backButtonTopPx}px;
    left: 24px;

    width: 80px;
    height: 80px;

    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
        content: '';

        display: block;
        width: ${backButtonIconSizePx}px;
        height: ${backButtonIconSizePx}px;

        background-image: url(${backButtonIcon});
        background-repeat: no-repeat;
        background-size: contain;
    }
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const logoSizePx = 72;

const StyledLogo = styled.div`
    width: ${logoSizePx}px;
    height: ${logoSizePx}px;
    border-radius: 16px;

    background-image: url(${logoIcon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledTitle = styled(HeadlineText4)`
    margin-left: 24px;

    display: inline-block;

    color: ${color.text.white};
`;

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export const Header: React.FC = ({ children, ...props }) => {
    const { title, isBackButtonVisible, isVisible } = useSelector(selectHeader);

    return (
        <StyledHeader isVisible={isVisible} {...props}>
            {isBackButtonVisible && <StyledBackButton />}
            <StyledSection>
                <StyledLogo />
                <StyledTitle>{title}</StyledTitle>
            </StyledSection>
            <StyledSection>
                <HeaderAddress />
                <HeaderCart />
            </StyledSection>
        </StyledHeader>
    );
};
