import React, { useEffect } from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { askForStockAction } from 'store/actions/server';
import { SHOW_ITEMS_MOCK } from 'mocks';

import { CatalogListTouch } from './List/CatalogList@touch';

export const CatalogTouch: React.FC = () => {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(askForStockAction());
    }, [dispatch]);

    useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'SHOW_ITEMS',
                    stock: SHOW_ITEMS_MOCK,
                },
            });

            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'ADD_ITEM',
                    item: '21ff407a-fa98-11e8-80c5-0cc47a817925',
                    count: '2',
                },
            });
        }
    }, []);

    return <CatalogListTouch />;
};
