import * as React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';

import { CatalogListItemTouch } from '../ListItem/CatalogListItem@touch';

const StyledCatalogList = styled.div`
    width: 100%;
    height: 100%;
    overflow-y: auto;
    padding: 6px 14px;
    box-sizing: border-box;
`;

const StyledScrollableCatalogContainer = styled.div`
    display: grid;
    grid-gap: 9px;
    grid-template-columns: 1fr 1fr;
`;

export const CatalogListTouch: React.FC = () => {
    const itemIds = useSelector(({ catalogPageTouch }) => catalogPageTouch.order);
    const itemsList = React.useMemo(() => itemIds.map((itemId) => <CatalogListItemTouch key={itemId} id={itemId} />), [
        itemIds,
    ]);

    return (
        <StyledCatalogList>
            <StyledScrollableCatalogContainer>{itemsList}</StyledScrollableCatalogContainer>
        </StyledCatalogList>
    );
};
