import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { useThrottledFn } from 'hooks/useThrottledFn';
import { selectHeader } from 'store/selectors';
import { ROUTES } from 'store/constants/router';
import { HeaderButton } from 'components/HeaderButton/HeaderButton';
import { icon } from 'style/const/icon';

import addressIcon from './address.svg';

const addressIconSizePx = icon.m.sizePx;

const StyledIcon = styled.div`
    width: ${addressIconSizePx}px;
    height: ${addressIconSizePx}px;

    background-image: url('${addressIcon}');
    background-repeat: no-repeat;
    background-size: contain;
`;

const throttleTimeoutMs = 200;

export const HeaderAddress: React.FC = () => {
    const dispatch = useDispatch();

    const { text, isVisible } = useSelector((state) => selectHeader(state).address);

    const handleClick = useThrottledFn(() => dispatch(push(ROUTES.ADDRESS_SIMPLE)), throttleTimeoutMs, undefined, [
        dispatch,
    ]);

    return (
        <HeaderButton isVisible={isVisible} onClick={handleClick} text={text}>
            <StyledIcon />
        </HeaderButton>
    );
};
