import React from 'react';
import styled from 'styled-components';
import { color } from 'style/const/color';
import { CaptionText, captionLineHeightPx } from 'components/Text/Caption';

export type BadgeType = 'sold-out' | 'few-left' | 'sale' | 'price-changed';

const contentMap: Record<BadgeType, string> = {
    'sold-out': 'Раскупили',
    'few-left': 'Осталось мало',
    'price-changed': 'Изменилась цена',
    sale: 'Скидка',
};

const backgroundColorMap: Record<BadgeType, string> = {
    'sold-out': color.background.default,
    'few-left': color.button.warning,
    'price-changed': color.button.warning,
    sale: color.button.primary,
};

const badgePaddingYPx = 8;
const badgeHeightPx = captionLineHeightPx + badgePaddingYPx * 2;
const badgeBorderRadiusPx = badgeHeightPx / 2;

interface StyledBadgeProps {
    badgeType: BadgeType;
}
const StyledBadge = styled(CaptionText)<StyledBadgeProps>`
    padding: ${badgePaddingYPx}px 16px;
    border-radius: ${badgeBorderRadiusPx}px;

    color: ${color.text.primary};
    background-color: ${({ badgeType }) => backgroundColorMap[badgeType]};
`;

export interface BadgeProps extends React.HTMLAttributes<HTMLDivElement> {
    badgeType: BadgeType;
}

export const Badge: React.FC<BadgeProps> = ({ badgeType, children, ...props }) => {
    return (
        <StyledBadge badgeType={badgeType} {...props}>
            {contentMap[badgeType]} {children}
        </StyledBadge>
    );
};
