import styled, { keyframes } from 'styled-components';

import spinnerImage from './spinner.svg';

const spinnerSizePx = 112;

const spinnerAnimationDurationMs = 1000;

export const Spinner = styled.div`
    width: ${spinnerSizePx}px;
    height: ${spinnerSizePx}px;

    background: url('${spinnerImage}');
    background-size: cover;

    animation-name: ${keyframes`
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    `};
    animation-duration: ${spinnerAnimationDurationMs}ms;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
`;
