import React from 'react';
import styled from 'styled-components';
import { Skeleton } from 'components/Skeleton/Skeleton';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';

import { cardWidthPx, cardHeightPx } from '../ListItem/CatalogListItem';
import { cardGapPx } from '../List/CatalogList';

const columnCount = Math.floor((layout.widthPx + cardGapPx) / (cardWidthPx + cardGapPx)) + 1;

const getWidthByCount = (count: number) => cardWidthPx * count + cardGapPx * (count - 1);

const skeletonWidthPx = getWidthByCount(columnCount);

const skeletonPaddingXPx = (layout.widthPx - getWidthByCount(columnCount - 1)) / 2;
const skeletonPaddingYPx = (layout.contentHeightPx - cardHeightPx) / 2;

const backgroundColor = color.background.surface;
const foregroundColor = color.button.secondary1;

const StyledRoot = styled.div`
    box-sizing: border-box;
    height: ${layout.contentHeightPx}px;
    padding-left: ${skeletonPaddingXPx}px;
    padding-top: ${skeletonPaddingYPx}px;
`;

export const CatalogSkeleton: React.FC = () => (
    <StyledRoot>
        <Skeleton
            width={skeletonWidthPx}
            height={cardHeightPx}
            backgroundColor={backgroundColor}
            foregroundColor={foregroundColor}
            itemWidth={cardWidthPx}
            itemHeight={cardHeightPx}
            borderRadius={40}
            rowCount={1}
            columnCount={columnCount}
            paddingBetweenRows={cardGapPx}
            paddingBetweenColumns={cardGapPx}
            speed={2}
        />
    </StyledRoot>
);
