import React from 'react';
import styled from 'styled-components';
import AddIcon from 'assets/icons/add.svg';
import RemoveIcon from 'assets/icons/remove.svg';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { useThrottledFn } from 'hooks/useThrottledFn';
import { addItemAction, removeItemAction } from 'store/actions/native';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { BadgeType } from 'components/Badge/Badge';

const throttleTimeoutMs = 150;
const cardBorderRadiusPx = 20;

const StyledListItem = styled.div`
    position: relative;
    border-radius: ${cardBorderRadiusPx}px;
    background-color: rgba(255, 255, 255, 0.06);
    color: #fff;
`;

interface StyledThumbProps {
    imageUrl: string;
}

const StyledThumb = styled.div<StyledThumbProps>`
    display: block;
    width: 100%;
    height: 168px;

    background-image: url('${({ imageUrl }) => imageUrl}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    border-top-left-radius: ${cardBorderRadiusPx}px;
    border-top-right-radius: ${cardBorderRadiusPx}px;
`;

const StyledIndexBadge = styled(IndexBadge)`
    top: 8px;
    right: 8px;
    color: #fff;
`;

type StyledIconProps = {
    icon: string;
};

const StyledIcon = styled.div<StyledIconProps>`
    height: 12px;
    width: 12px;

    background-image: url(${({ icon }) => icon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledDescription = styled.div`
    padding: 8px 16px 0;
`;

const StyledButtonsContainer = styled.div`
    padding: 12px;
    margin-top: 6px;
    display: flex;
    align-items: center;
    justify-content: space-between;
`;

const StyledName = styled.div`
    color: #fff;
    font-size: 14px;
    height: 36px;

    display: block;
    display: -webkit-box;

    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
`;

type StyledTouchButtonProps = {
    disabled?: boolean;
};

const StyledTouchButton = styled.div<StyledTouchButtonProps>`
    cursor: pointer;
    padding: 11px 20px;
    text-align: center;
    font-size: 14px;
    background-color: rgba(255, 255, 255, 0.06);
    border-radius: 12px;
    width: 100%;
    opacity: ${({ disabled }) => (disabled ? 0.2 : 1)};
`;

const StyledCountTouchButton = styled.div`
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.06);
    border-radius: 12px;
    display: flex;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 14px;
`;

const StyledCount = styled.div`
    font-size: 16px;
`;

interface CatalogListItemTouchProps {
    id: string;
}

export interface CatalogListItemTouchViewProps {
    imageUrl: string;
    count: number;
    name: string;
    index: number;
    price: number;
    badge?: BadgeType;
    disabled?: boolean;
    buttonDisabled?: boolean;
}

export const CatalogListItemTouch: React.FC<CatalogListItemTouchProps> = ({ id }) => {
    const dispatch = useDispatch();
    const { imageUrl, count, index, name, price, disabled } = useSelector(
        ({ catalogPageTouch }) => catalogPageTouch.itemsMap[id],
    );
    const isPossibleChangeCountActive = count > 0;

    const handleClickAdd = useThrottledFn(
        () => {
            if (!disabled) {
                dispatch(addItemAction({ item: id, count: '1' }));
            }
        },
        throttleTimeoutMs,
        undefined,
        [dispatch, id, disabled],
    );

    const handleClickRemove = useThrottledFn(
        () => {
            if (!disabled) {
                dispatch(removeItemAction({ item: id, count: '1' }));
            }
        },
        throttleTimeoutMs,
        undefined,
        [dispatch, id, disabled],
    );

    return (
        <StyledListItem tabIndex={0}>
            <StyledThumb imageUrl={imageUrl} />
            <StyledIndexBadge size="xs" index={index} />
            <StyledDescription>
                <StyledName>{name}</StyledName>
            </StyledDescription>
            <StyledButtonsContainer>
                {isPossibleChangeCountActive ? (
                    <>
                        <StyledCountTouchButton onClick={handleClickRemove}>
                            <StyledIcon icon={RemoveIcon} />
                        </StyledCountTouchButton>
                        <StyledCount>{count}</StyledCount>
                        <StyledCountTouchButton onClick={handleClickAdd}>
                            <StyledIcon icon={AddIcon} />
                        </StyledCountTouchButton>
                    </>
                ) : (
                    <StyledTouchButton onClick={handleClickAdd} disabled={disabled}>
                        {price} ₽
                    </StyledTouchButton>
                )}
            </StyledButtonsContainer>
        </StyledListItem>
    );
};
