import { AppReducer } from 'store/constants';
import { ScenarioUser, StoreUser } from 'store/constants/user';

const normalizeUserData = (data: ScenarioUser): StoreUser => {
    return {
        customerName: data.customer_name,
        phoneNumber: data.phone_num,
        latitude: data.address.location.latitude,
        longitude: data.address.location.longitude,
        address: data.address.address_string,
        city: data.address.city,
        street: data.address.street,
        house: data.address.house,
        building: data.address.building,
        floor: data.address.floor,
        entrance: data.address.floor,
        apartment: data.address.apartment,
        doorCode: data.address.door_code,
    };
};

export const userData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'GO_TO_CATALOG': {
            state.userData = normalizeUserData(action.payload.address);
            break;
        }

        case 'ADDRESS_INPUT': {
            const { latitude, longitude } = action.payload.device_location;
            state.userData.latitude = latitude;
            state.userData.longitude = longitude;

            break;
        }

        case 'ADDRESS_FULL_SUCCESS': {
            state.userData.phoneNumber = state.userModel.phoneNumber;
            state.userData.apartment = state.userModel.apartment;
            state.userData.entrance = state.userModel.entrance;
            state.userData.floor = state.userModel.floor;

            break;
        }

        default:
            break;
    }
};
