import { AppState, initialState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';
import { DGBuilding } from 'types/2gis-maps';

const MAX_PHONE_NUMBER_SYMBOLS_COUNT = 12;

const updateUserAddressBasedOnBuilding = ({ userModel }: AppState, building: DGBuilding) => {
    // eslint-disable-next-line @typescript-eslint/camelcase
    const { point, address, name, full_name } = building;
    const [addressComponent] = address.components;
    const { street, number } = addressComponent;
    // eslint-disable-next-line @typescript-eslint/camelcase
    const [city] = full_name.split(', ');

    userModel.city = city;
    userModel.street = street;
    userModel.house = number;
    userModel.address = name;
    userModel.latitude = String(point.lat);
    userModel.longitude = String(point.lon);
};

const normalizePhone = (phoneNumber: string) => {
    let result = Array.from(phoneNumber).filter((symbol, index) => {
        return /[0-9]/.test(symbol) || (index === 0 && symbol === '+');
    });
    const [firstSymbol, ...otherSymbols] = result;

    if (firstSymbol === '8' || firstSymbol === '7') {
        result = ['+', '7', ...otherSymbols];
    }

    return result.slice(0, MAX_PHONE_NUMBER_SYMBOLS_COUNT).join('');
};

export const userModel: AppReducer = (state, action) => {
    switch (action.type) {
        case 'address/CHANGE_INPUT_VALUE': {
            state.userModel = { ...initialState.userModel };
            state.userModel.address = action.payload;

            break;
        }

        case 'address/CLICK_SUGGEST_ITEM': {
            const suggestItem = state.addressSimplePage.suggestsRaw[action.payload];

            if (suggestItem.type === 'street') {
                state.userModel.address = suggestItem.name;
            } else if (suggestItem.type === 'building') {
                updateUserAddressBasedOnBuilding(state, suggestItem);
            }

            break;
        }

        case 'ADDRESS_INPUT': {
            state.userModel = { ...initialState.userModel };
            state.userModel.address = action.payload.input;

            break;
        }

        case 'ADDRESS_INPUT_ADD': {
            state.userModel = { ...initialState.userModel };
            state.userModel.address = action.payload.input;

            break;
        }

        case 'ADDRESS_INPUT_CLEAR': {
            state.userModel = { ...initialState.userModel };

            break;
        }

        case 'ADDRESS_FULL_INPUT': {
            if (action.payload.input.apartment) {
                state.userModel.apartment = action.payload.input.apartment;
            }

            if (action.payload.input.floor) {
                state.userModel.floor = action.payload.input.floor;
            }

            if (action.payload.input.entrance) {
                state.userModel.entrance = action.payload.input.entrance;
            }

            if (action.payload.input.phone_num) {
                state.userModel.phoneNumber = normalizePhone(action.payload.input.phone_num);
            }

            break;
        }

        case 'ADDRESS_FULL_INPUT_ADD': {
            const pathname = selectPathname(state);

            switch (pathname) {
                case ROUTES.ADDRESS_FULL_PHONE: {
                    state.userModel.phoneNumber = normalizePhone(action.payload.input);

                    break;
                }

                case ROUTES.ADDRESS_FULL_APARTMENT: {
                    state.userModel.apartment = action.payload.input;

                    break;
                }

                case ROUTES.ADDRESS_FULL_ENTRANCE: {
                    state.userModel.entrance = action.payload.input;

                    break;
                }

                case ROUTES.ADDRESS_FULL_FLOOR: {
                    state.userModel.floor = action.payload.input;

                    break;
                }

                default:
                    break;
            }

            break;
        }

        case 'ADDRESS_FULL_INPUT_CLEAR': {
            const pathname = selectPathname(state);

            switch (pathname) {
                case ROUTES.ADDRESS_FULL_PHONE: {
                    state.userModel.phoneNumber = '';
                    break;
                }

                case ROUTES.ADDRESS_FULL_APARTMENT: {
                    state.userModel.apartment = '';
                    break;
                }

                case ROUTES.ADDRESS_FULL_ENTRANCE: {
                    state.userModel.entrance = '';
                    break;
                }

                case ROUTES.ADDRESS_FULL_FLOOR: {
                    state.userModel.floor = '';
                    break;
                }

                case ROUTES.ADDRESS_FULL_ALL: {
                    state.userModel.phoneNumber = '';
                    state.userModel.apartment = '';
                    state.userModel.entrance = '';
                    state.userModel.floor = '';
                    break;
                }

                default:
                    break;
            }

            break;
        }

        case 'addressFull/PHONE_NUMBER_INPUT_CHANGE': {
            const { inputType, data } = action.payload;

            if (inputType === 'insertText') {
                state.userModel.phoneNumber = normalizePhone(state.userModel.phoneNumber + data);
            } else {
                state.userModel.phoneNumber = state.userModel.phoneNumber.slice(0, -1);
            }

            break;
        }

        case 'addressFull/APARTMENT_INPUT_CHANGE': {
            state.userModel.apartment = action.payload;

            break;
        }

        case 'addressFull/ENTRANCE_INPUT_CHANGE': {
            state.userModel.entrance = action.payload;

            break;
        }

        case 'addressFull/FLOOR_INPUT_CHANGE': {
            state.userModel.floor = action.payload;

            break;
        }

        case 'GO_TO_CATALOG': {
            state.userModel = { ...state.userData };

            break;
        }

        default:
            break;
    }
};
