// 1. (?=(\d{3})+$) - матчит подстроки нулевой длины, после которых идет три цифры подряд
// 2. (?=...) - positive lookahead, конструкция, которая матчит позицию, после которой срабатывает указанный паттерн
// 3. (?:\d{3})+ - повторяющаяся незахватывающая группа, которая матчит подстроки из трех цифр
// 4. $ - EOL - чтобы выровнять [3] по концу строки
export const splitByThousands = (n: number) => n.toString().replace(/(?=(?:\d{3})+$)/g, ' ');
