import { AppReducer, AppState } from 'store/constants';
import { OrderErrorPayload } from 'store/constants/native';

const mapOrderErrorTypeToProps: Record<OrderErrorPayload['type'], AppState['errorPage']> = {
    partner: {
        title: 'Не получилось оформить заказ',
        subtitle: 'Пожалуйста, попробуйте ещё раз',
        retryButtonText: 'Повторить',
        closeButtonText: 'Очень жаль',
    },
    payment: {
        title: 'Не получилось оформить заказ',
        subtitle: 'Пожалуйста, попробуйте ещё раз',
        retryButtonText: 'Повторить',
        closeButtonText: 'Очень жаль',
    },
    timing: {
        title: 'Закрыто',
        subtitle: 'Часы работы с 8:00 до 23:00, \nбез обеда и выходных',
        retryButtonText: 'Повторить',
        closeButtonText: 'Зайду позже',
    },
};

export const errorPageView: AppReducer = (state, action) => {
    switch (action.type) {
        case 'ORDER_ERROR': {
            state.errorPage = mapOrderErrorTypeToProps[action.payload.type];
            break;
        }
        default: {
            break;
        }
    }
};
