import { DGSuggestItem, DGSuggestResponse } from 'types/2gis-maps';
import { makeRequest } from 'tools/request';

const DG_KEY = 'ruvqwj0703';

interface GetSuggestsParams {
    q: string;
    latitude: string;
    longitude: string;
}
export const getSuggests = ({ q, longitude, latitude }: GetSuggestsParams): Promise<DGSuggestItem[]> => {
    if (q === '') {
        return Promise.resolve([] as DGSuggestItem[]);
    }

    return Promise.resolve()
        .then(() => {
            const params = {
                q,
                key: DG_KEY,
                local: 'ru_RU',
                fields: ['items.address', 'items.point'].join(','),
                type: ['building', 'street'].join(','),
                // высчитываем левую верхнюю и правую нижнюю точки так, чтобы
                // радиус вокруг исходной точки был примерно 40км
                viewpoint1: [Number(longitude) - 0.4, Number(latitude) + 0.4].join(','),
                viewpoint2: [Number(longitude) + 0.4, Number(latitude) - 0.4].join(','),
                // eslint-disable-next-line @typescript-eslint/camelcase
                is_viewport_change: false,
            };

            return makeRequest('https://catalog.api.2gis.ru/3.0/suggests', params);
        })
        .then((response) => {
            if (response.meta.code !== 200) {
                throw response.meta.error;
            }

            return response;
        })
        .then(({ result }: DGSuggestResponse) => {
            if (!result) {
                return [];
            }

            return result.items;
        });
};
