import React from 'react';
import ContentLoader, { IContentLoaderProps } from 'react-content-loader';

interface SkeletonProps extends IContentLoaderProps {
    itemWidth: number;
    itemHeight: number;
    borderRadius: number;
    rowCount: number;
    columnCount: number;
    paddingBetweenColumns: number;
    paddingBetweenRows: number;
}

export const Skeleton: React.FC<SkeletonProps> = ({
    itemWidth,
    itemHeight,
    borderRadius,
    rowCount,
    columnCount,
    paddingBetweenColumns,
    paddingBetweenRows,
    ...props
}) => {
    const list = Array.from(Array(columnCount)).map((_, columnIndex) =>
        // eslint-disable-next-line no-shadow
        Array.from(Array(rowCount)).map((_, rowIndex) => (
            <rect
                key={`${columnIndex} ${rowIndex}`}
                x={columnIndex * (itemWidth + paddingBetweenColumns)}
                y={rowIndex * (itemHeight + paddingBetweenRows)}
                width={itemWidth}
                height={itemHeight}
                rx={borderRadius}
                ry={borderRadius}
            />
        )),
    );

    return <ContentLoader {...props}>{list}</ContentLoader>;
};
