import { ButtonProps } from 'components/Button/Button';
import { splitByThousands } from 'tools/split-by-thousands';
import { selectProductById, selectCartItemById } from 'store/selectors';
import { AppReducer, AppState } from 'store/constants';
import { formatPhoneNumber } from 'tools/format-phone-number';

const prepareHasRequiredData = ({ userData: { phoneNumber, address } }: AppState) => Boolean(address && phoneNumber);

const prepareItemsMap = (state: AppState) => {
    const { cartPage } = state;

    return cartPage.order.reduce<AppState['cartPage']['itemsMap']>((result, itemId) => {
        const cartItem = selectCartItemById(state, itemId);
        const { imageUrl, quantityLeft, price, name } = selectProductById(state, itemId);

        if (cartItem) {
            result[itemId] = {
                imageUrl,
                name,
                price: Math.ceil(price / 100),
                count: cartItem.count,
                disabled: quantityLeft === 0,
                moreButtonDisabled: quantityLeft === cartItem.count,
            };
        }

        return result;
    }, {});
};

const itemHeight = 152;

const getWrapperYShift = ({ cartPage: { selectedItemIndex, order } }: AppState) => {
    if (selectedItemIndex === -1) {
        return 0;
    }

    // так как индекс начинается с 0, то расчеты бы имели смысл начиная
    // со второго элемента (индекс = 1), но нам нужно начинать скролл с
    // 3-го элемента, поэтому сдвигаем значение на 1
    let relatedIndex = Math.max(0, selectedItemIndex - 1);

    // ограничиваем максимальное значение индекса
    relatedIndex = Math.min(Math.max(0, order.length - 4), relatedIndex);

    // домножаем отступ на коэффициент высоты айтема
    return relatedIndex * itemHeight;
};

const prepareMakePurchaseButtonProps = (store: AppState): ButtonProps => {
    const {
        cart: { totalPrice, minimalPrice },
        cartPage: { isOrderStatusWaiting },
    } = store;

    const disabled = isOrderStatusWaiting || totalPrice < minimalPrice;

    return {
        disabled,
        buttonType: !disabled ? 'primary' : 'secondary',
    };
};

const prepareToMinimumPrice = ({ cart: { minimalPrice, totalPrice } }: AppState) =>
    Math.max(0, Math.ceil((minimalPrice - totalPrice) / 100));

const prepareTotalPrice = ({ cart: { totalPrice } }: AppState) => splitByThousands(Math.ceil(totalPrice / 100));

const prepareTopGradientVisible = ({ cartPage: { wrapperYShift } }: AppState) => wrapperYShift !== 0;

const prepareBottomGradientVisible = ({ cartPage: { wrapperYShift, order } }: AppState) =>
    order.length > 4 && wrapperYShift !== (order.length - 4) * itemHeight;

const getFormattedPhoneNumber = ({ userData }: AppState) =>
    userData.phoneNumber && formatPhoneNumber(userData.phoneNumber);

const getFormattedPersonalAddress = ({ userData: { apartment, entrance, floor } }: AppState) =>
    [
        apartment && `квартира ${apartment}`, //
        entrance && `подъезд ${entrance}`,
        floor && `этаж ${floor}`,
    ]
        .filter(Boolean)
        .join(', ');

export const cartPageView: AppReducer = (state, action) => {
    const { cart, cartPage } = state;

    switch (action.type) {
        case 'cart/SELECT_ITEM': {
            cartPage.selectedItemIndex = cartPage.order.indexOf(action.payload);

            break;
        }

        case 'GO_TO_WAIT': {
            cartPage.isOrderStatusWaiting = true;

            break;
        }

        case 'VALIDATION_OK': {
            cartPage.isOrderStatusWaiting = false;

            break;
        }

        default:
            break;
    }

    cartPage.order = cart.order;
    cartPage.deliveryTime = cart.deliveryTime;
    cartPage.itemsMap = prepareItemsMap(state);
    cartPage.totalPrice = prepareTotalPrice(state);
    cartPage.toMinimumPrice = prepareToMinimumPrice(state);
    cartPage.wrapperYShift = getWrapperYShift(state);
    cartPage.makePurchaseButton = prepareMakePurchaseButtonProps(state);
    cartPage.topGradientVisible = prepareTopGradientVisible(state);
    cartPage.bottomGradientVisible = prepareBottomGradientVisible(state);
    cartPage.hasRequiredData = prepareHasRequiredData(state);
    cartPage.phoneNumber = getFormattedPhoneNumber(state);
    cartPage.personalAddress = getFormattedPersonalAddress(state);
};
