import { CatalogListItemViewProps } from 'pages/Catalog/ListItem/CatalogListItem';
import { AppState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { selectProductById, selectPathname } from 'store/selectors';
import { calculateButtonDisabled, prepareCount, preparePrice } from 'store/reducers/helpers/catalogPage';

const screenWidth = 1920;
const cardWidth = 392;
const cardGap = 32;

const screenItemCount = Math.floor(screenWidth / (cardWidth + cardGap));

const listOffset = (-1 * (screenWidth % (cardWidth + cardGap))) / 2;

const itemWidth = cardWidth + cardGap;

const getListIndexOffset = ({ catalogPage: { selectedItemId, order, listIndexOffset } }: AppState) => {
    const selectedItemIndex = order.indexOf(selectedItemId);

    if (selectedItemIndex === -1) {
        return 0;
    }

    const scrollPageSize = 1;

    if (selectedItemIndex <= listIndexOffset) {
        return Math.max(0, listIndexOffset - scrollPageSize);
    }

    if (selectedItemIndex >= listIndexOffset + screenItemCount - 1) {
        return Math.min(listIndexOffset + scrollPageSize, order.length - screenItemCount);
    }

    return listIndexOffset;
};

const getWrapperXShift = ({ catalogPage: { listIndexOffset } }: AppState) =>
    listOffset + listIndexOffset * itemWidth - cardGap / 2;

const getScreenIndexMap = ({ catalogPage: { listIndexOffset, order } }: AppState): Record<string, number> =>
    new Array(screenItemCount).fill(0).reduce(
        (result, _, index) => ({
            ...result,
            [order[index + listIndexOffset]]: index + 1,
        }),
        {} as Record<string, number>,
    );

const prepareBadge = ({ cart, products }: AppState, itemId: string): CatalogListItemViewProps['badge'] => {
    const cartItem = cart.itemsMap[itemId];
    const productItem = products.itemsMap[itemId];

    if (productItem.quantityLeft === 0) {
        return 'sold-out';
    }

    if (cartItem && cartItem.count === productItem.quantityLeft) {
        return 'few-left';
    }

    return undefined;
};

const prepareIndex = ({ catalogPage: { screenIndexMap } }: AppState, itemId: string) => screenIndexMap[itemId];

const prepareCatalogItemsMap = (state: AppState) => {
    const { products, catalogPage } = state;

    return Object.keys(products.itemsMap).reduce<AppState['catalogPage']['itemsMap']>((acc, itemId) => {
        const productItem = selectProductById(state, itemId);

        acc[itemId] = {
            shouldFocusOnMount: catalogPage.selectedItemId === itemId,
            imageUrl: productItem.imageUrl,
            name: productItem.name,
            price: preparePrice(productItem.price),
            count: prepareCount(state, itemId),
            index: prepareIndex(state, itemId),
            badge: prepareBadge(state, itemId),
            isButtonDisabled: calculateButtonDisabled(state, itemId),
            disabled: productItem.quantityLeft === 0,
        };

        return acc;
    }, {});
};

const getSelectedItemId = ({ catalogPage: { order, selectedItemId } }: AppState) => {
    if (selectedItemId) {
        return selectedItemId;
    }

    const [firstItemId] = order;

    return firstItemId;
};

export const catalogPageView: AppReducer = (state, action) => {
    const { products, catalogPage } = state;

    switch (action.type) {
        case 'ADD_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                catalogPage.selectedItemId = action.payload.item;
            }

            break;
        }

        case 'catalog/SELECT_ITEM': {
            catalogPage.selectedItemId = action.payload;

            break;
        }

        default:
            break;
    }

    catalogPage.order = Object.keys(products.itemsMap);
    catalogPage.selectedItemId = getSelectedItemId(state);
    catalogPage.listIndexOffset = getListIndexOffset(state);
    catalogPage.wrapperXShift = getWrapperXShift(state);
    catalogPage.screenIndexMap = getScreenIndexMap(state);
    catalogPage.itemsMap = prepareCatalogItemsMap(state);
};
