import { addressFullSuccessAction } from 'store/actions/native';
import { AppThunkAction } from 'store/constants';
import { goBack, replace } from 'connected-react-router';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';

export const addressFullValidationOkThunk = (): AppThunkAction => (dispatch, getState) => {
    const state = getState();
    const {
        addressFullPage: { nextFocus },
    } = state;
    const currentRoute = selectPathname(state);

    if (nextFocus === ROUTES.ADDRESS_FULL_ALL || nextFocus === currentRoute) {
        dispatch(goBack());
    } else {
        dispatch(replace(nextFocus));
    }
};

export const addressFullSuccessThunk = (): AppThunkAction => (dispatch) => {
    dispatch(addressFullSuccessAction());

    dispatch(goBack());
};
