export const Promo: React.FC = () => {
    const dispatch = useDispatch();
    // const currentIndex = useIndex(popcornList.length);

    const handleClickSubmitButton = React.useCallback(() => {
        dispatch(push(ROUTES.ADDRESS_SIMPLE));
    }, [dispatch]);
    const handleCloseButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    return (
        <>
            <TextContainer>
                <Title>Доставляем попкорн к просмотру фильмов и сериалов</Title>
                <Subtitle>Бесплатно в течении 15-30 минут для Москвы и Санкт-Петербурга</Subtitle>
                <ButtonContainer>
                    <StyledButton shouldFocusOnMount onClick={handleClickSubmitButton}>
                        Указать адрес
                    </StyledButton>
                    <StyledButton onClick={handleCloseButtonClick} buttonType="secondary">
                        Не сейчас
                    </StyledButton>
                </ButtonContainer>
            </TextContainer>
            <PopcornContainer>
                <Popcorn isVisible popcornStyle={popcornList[0]} />
                {/* {popcornList.map((style, index) => (
                    <Popcorn key={index} isVisible={index === 0} popcornStyle={style} />
                ))} */}
            </PopcornContainer>
        </>
    );
};
