import React from 'react';
import { addressFullValidationOkThunk } from 'store/thunks/addressFullPage';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullApartmentInputChangeAction } from 'store/actions/addressFullPage';
import { Input } from 'components/Input/Input';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullApartment = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.apartmentInputValue);
    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullApartmentInputChangeAction(e.target.value));
        },
        [dispatch],
    );
    const handleInputSubmit = React.useCallback(() => {
        dispatch(addressFullValidationOkThunk());
    }, [dispatch]);

    return (
        <StyledInput
            title="Квартира"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
        />
    );
};
