import { css, FlattenSimpleInterpolation } from 'styled-components';
import { focus } from 'style/const/focus';

export const syntheticFocus = (ruleset: FlattenSimpleInterpolation, isFocused?: boolean) => {
    if (isFocused === undefined) {
        return css`
            &:focus {
                ${ruleset}
            }
        `;
    }

    return isFocused && ruleset;
};

interface FocusMixinOptions {
    isFocused?: boolean;
    borderRadius?: number;
}

const positionPx = -1 * (focus.paddingPx + focus.borderSizePx);

export const focusBorderMixin = ({ isFocused, borderRadius }: FocusMixinOptions) => css`
    &::after {
        content: '';

        position: absolute;
        top: ${positionPx}px;
        bottom: ${positionPx}px;
        left: ${positionPx}px;
        right: ${positionPx}px;

        display: block;
        box-sizing: border-box;

        border-width: ${focus.borderSizePx}px;
        border-color: ${focus.color};
        border-style: solid;
        border-radius: ${borderRadius ? borderRadius + focus.paddingPx * 2 : 0}px;

        opacity: 0;

        transition: opacity ${focus.transition};

        z-index: 2;
        pointer-events: none;
    }

    ${syntheticFocus(
        css`
            &::after {
                opacity: 1;
            }
        `,
        isFocused,
    )}
`;
