import { AppState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';

const isOverlayVisible = (state: AppState) => {
    switch (selectPathname(state)) {
        case ROUTES.CART:
            return state.cartPage.isOrderStatusWaiting;
        default:
            return false;
    }
};

const getOverlayText = (state: AppState) => {
    switch (selectPathname(state)) {
        case ROUTES.CART: {
            return state.cartPage.isOrderStatusWaiting ? 'Оформляем заказ' : '';
        }
        default:
            return '';
    }
};

export const layoutView: AppReducer = (state) => {
    const { layout } = state;

    layout.overlay.isVisible = isOverlayVisible(state);
    layout.overlay.text = getOverlayText(state);
};
