import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { selectHeader } from 'store/selectors';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout@touch';
import { transition } from 'style/const/transition';
import { BodyText2 } from 'components/Text/Body@touch';

import { HeaderAddressTouch } from '../HeaderAddress/HeaderAddress@touch';
import { HeaderCartTouch } from '../HeaderCart/HeaderCart@touch';

import backButtonIcon from './Header.assets/chevron-left.svg';
import logoIcon from './Header.assets/logo.svg';

interface StyledHeaderProps {
    isVisible: boolean;
}

const StyledHeader = styled.div<StyledHeaderProps>`
    position: relative;

    height: ${layout.headerHeightPx}px;

    box-sizing: border-box;
    padding: 14px 13px 9px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    --spatial-navigation-contain: contain;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const backButtonIconSizePx = icon.xs.sizePx;

const StyledBackButton = styled.span`
    width: 32px;
    height: 32px;
    margin-right: 9px;

    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
        content: '';

        display: block;
        width: ${backButtonIconSizePx}px;
        height: ${backButtonIconSizePx}px;

        background-image: url(${backButtonIcon});
        background-repeat: no-repeat;
        background-size: contain;
    }
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const StyledLogo = styled.div`
    margin-left: 3px;

    width: 28px;
    height: 28px;
    border-radius: 8px;

    background-image: url(${logoIcon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledTitle = styled(BodyText2)`
    margin-left: 10px;

    display: inline-block;

    color: ${color.text.white};
`;

// eslint-disable-next-line  @typescript-eslint/no-unused-vars
export const HeaderTouch: React.FC = ({ children, ...props }) => {
    const { title, isBackButtonVisible, isVisible } = useSelector(selectHeader);

    return (
        <StyledHeader isVisible={isVisible} {...props}>
            <StyledSection>
                {isBackButtonVisible && <StyledBackButton />}
                <StyledLogo />
                <StyledTitle>{title}</StyledTitle>
            </StyledSection>
            <StyledSection>
                <HeaderAddressTouch />
                <HeaderCartTouch />
            </StyledSection>
        </StyledHeader>
    );
};
