import React from 'react';
import styled from 'styled-components';
import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { closeAppAction } from 'store/actions/app';
import { Button } from 'components/Button/Button';
import { HeadlineText1 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';

import circlesImage from './Error.assets/circles.svg';
import warningIcon from './Error.assets/warning.svg';

const ErrorPage = styled.div`
    position: relative;

    height: ${layout.contentHeightPx}px;
    padding-left: 128px;
`;

const TextContainer = styled.div`
    width: 816px;
    height: ${layout.contentHeightPx}px;

    display: flex;
    flex-direction: column;
    justify-content: center;

    align-content: left;
`;

const Title = styled(HeadlineText1)`
    margin-bottom: 12px;

    color: ${color.text.primary};
`;

const Subtitle = styled(BodyText1)`
    margin-bottom: 48px;

    white-space: pre-wrap;

    color: ${color.text.secondary};
`;

const ButtonContainer = styled.div`
    display: flex;
    flex-direction: row;
    margin: 0 -24px;
`;

const StyledButton = styled(Button)`
    margin: 0 24px;
`;

const circlesPositionXPx = -266;
const circlesPositionYPx = -1 * (layout.headerHeightPx + 280);

const Circles = styled.div`
    position: absolute;
    top: ${circlesPositionYPx}px;
    right: ${circlesPositionXPx}px;

    box-sizing: border-box;
    width: 1596px;
    height: 1596px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url('${circlesImage}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

const WarningIcon = styled.div`
    width: 384px;
    height: 384px;

    background-image: url('${warningIcon}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

export const Error: React.FC = () => {
    const { title, subtitle, retryButtonText, closeButtonText } = useSelector(({ errorPage }) => errorPage);

    const dispatch = useDispatch();

    const handleRetryButtonClick = React.useCallback(() => {
        dispatch(goBack());
    }, [dispatch]);

    const handleCloseButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    return (
        <ErrorPage>
            <TextContainer>
                <Title>{title}</Title>
                <Subtitle>{subtitle}</Subtitle>
                <ButtonContainer>
                    <StyledButton buttonType="secondary" onClick={handleRetryButtonClick}>
                        {retryButtonText}
                    </StyledButton>
                    {closeButtonText ? (
                        <StyledButton buttonType="secondary" onClick={handleCloseButtonClick}>
                            {closeButtonText}
                        </StyledButton>
                    ) : null}
                </ButtonContainer>
            </TextContainer>
            <Circles>
                <WarningIcon />
            </Circles>
        </ErrorPage>
    );
};
