import React from 'react';
import styled from 'styled-components';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { color } from 'style/const/color';
import { focusBorderMixin } from 'style/mixin/focus';
import { BodyText2 } from 'components/Text/Body';

type ButtonType = 'primary' | 'secondary';

const buttonTypeBackgroundMap: Record<ButtonType, string> = {
    primary: color.button.primary,
    secondary: color.button.secondary1,
};

const buttonBorderRadiusPx = 24;

interface StyledButtonProps {
    buttonType: ButtonType;
}
const StyledButton = styled(BodyText2)<StyledButtonProps>`
    position: relative;

    padding: 20px 40px;
    border-radius: ${buttonBorderRadiusPx}px;
    border: none;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background-color 0.3s ease-in-out;

    background-color: ${({ buttonType }) => buttonTypeBackgroundMap[buttonType]};
    color: ${color.text.primary};

    ${focusBorderMixin({ borderRadius: buttonBorderRadiusPx })}

    &[disabled] {
        opacity: 0.5;
    }
`;

export interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    shouldFocusOnMount?: boolean;
    forwardRef?: React.RefObject<unknown>;
    buttonType?: ButtonType;
}

export const Button: React.FC<ButtonProps> = ({ className, shouldFocusOnMount, forwardRef, buttonType, ...props }) => {
    const mountFocusRef = useFocusOnMount<HTMLButtonElement>();
    const ref = forwardRef || shouldFocusOnMount ? mountFocusRef : null;

    return (
        <StyledButton
            as="button"
            className={className}
            ref={ref}
            type="button"
            buttonType={buttonType || 'primary'}
            {...props}
        />
    );
};
