import { useThrottledFn as _useThrottledFn, ThrottleOrDebounceOpts } from 'beautiful-react-hooks';

const defaultOptions: ThrottleOrDebounceOpts = {
    leading: true,
    trailing: false,
};

export const useThrottledFn: typeof _useThrottledFn = (fn, timeout, options = defaultOptions, dependencies) =>
    _useThrottledFn(fn, timeout, options, dependencies);
