import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { useThrottledFn } from 'hooks/useThrottledFn';
import { removeItemAction, addItemAction } from 'store/actions/native';
import { cartSelectItemAction } from 'store/actions/cartPage';
import { Button } from 'components/Button/Button';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { Label } from 'components/Label/Label';
import { BodyText1, BodyText2 } from 'components/Text/Body';
import { FootnoteText1 } from 'components/Text/Footnote';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';
import { transition } from 'style/const/transition';
import { focusBorderMixin } from 'style/mixin/focus';
import addIcon from 'assets/icons/add.svg';
import removeIcon from 'assets/icons/remove.svg';

const thumbSizePx = 120;

interface StyledThumbProps {
    imageUrl: string;
}

const StyledThumb = styled.div<StyledThumbProps>`
    position: relative;

    margin-right: 32px;

    height: ${thumbSizePx}px;
    width: ${thumbSizePx}px;

    border-radius: 24px;

    background-image: url('${({ imageUrl }) => imageUrl}');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
`;

const StyledInfo = styled.div`
    margin-right: 48px;

    width: 384px;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledNameProps {
    oneLine?: boolean;
}

const StyledName = styled(BodyText1)<StyledNameProps>`
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: ${({ oneLine }) => (oneLine ? 1 : 2)};
    overflow: hidden;

    color: ${color.text.primary};
`;

const StyledPrice = styled(FootnoteText1)`
    margin-top: 4px;

    color: ${color.text.secondary};
`;

const StyledLabel = styled(Label)`
    margin-top: 4px;
`;

const StyledSelector = styled.div`
    box-sizing: border-box;
    padding: 8px;
    width: 216px;

    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const StyledSelectorValue = styled(BodyText2)`
    color: ${color.text.primary};
`;

type SelectorButtonIconType = 'less' | 'more' | 'remove';

const iconTypeIconMap: Record<SelectorButtonIconType, string> = {
    less: removeIcon,
    more: addIcon,
    remove: addIcon,
};

const selectorButtonSizePx = 64;
const selectorBorderRadiusPx = selectorButtonSizePx / 2;
const selectorIconSizePx = icon.s.sizePx;

interface StyledSelectorButtonProps {
    iconType: SelectorButtonIconType;
}
const StyledSelectorButton = styled.button<StyledSelectorButtonProps>`
    position: relative;

    height: ${selectorButtonSizePx}px;
    width: ${selectorButtonSizePx}px;
    border: none;
    border-radius: ${selectorBorderRadiusPx}px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: ${color.button.secondary1};

    will-change: background-color, transform, opacity;
    transition: ${transition.default};

    &::before {
        content: '';

        height: ${selectorIconSizePx}px;
        width: ${selectorIconSizePx}px;

        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &[disabled] {
        opacity: 0.3;
    }

    ${focusBorderMixin({ borderRadius: selectorBorderRadiusPx })}

    &::before {
        background-image: url('${({ iconType }) => iconTypeIconMap[iconType]}');
    }

    ${({ iconType }) =>
        iconType === 'remove' &&
        css`
            transform: rotate(45deg);
            background: ${color.button.critical};
        `}
`;

interface StyledCartListItemProps {
    disabled?: boolean;
}

const StyledCartListItem = styled.div<StyledCartListItemProps>`
    position: relative;

    box-sizing: border-box;
    height: 152px;
    width: 832px;
    padding: 16px;

    display: flex;
    align-items: center;

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledThumb},
            ${StyledName},
            ${StyledPrice} {
                opacity: .24;
            }

            ${StyledPrice} {
                color: ${color.text.primary};
            }
        `}
`;

const StyledIndexBadge = styled(IndexBadge)`
    top: 4px;
    right: 4px;
`;

export interface CartListItemProps {
    id: string;
    index: number;
}

export interface CartListItemViewProps {
    imageUrl: string;
    name: string;
    price: number;
    count: number;
    disabled?: boolean;
    moreButtonDisabled?: boolean;
}

const throttleTimeoutMs = 100;

export const CartListItem: React.FC<CartListItemProps> = ({ index, id }) => {
    const { imageUrl, name, price, count, disabled, moreButtonDisabled } = useSelector(
        ({ cartPage: { itemsMap } }) => itemsMap[id],
    );
    const dispatch = useDispatch();

    const handleLessClick = useThrottledFn(
        () => {
            dispatch(
                removeItemAction({
                    item: id,
                    count: '1',
                    shouldRemoveMention: !count,
                }),
            );
        },
        throttleTimeoutMs,
        undefined,
        [dispatch, id, count],
    );

    const handleMoreClick = useThrottledFn(
        () => {
            dispatch(
                addItemAction({
                    item: id,
                    count: '1',
                }),
            );
        },
        throttleTimeoutMs,
        undefined,
        [dispatch, id],
    );

    const handleRemoveClick = useThrottledFn(
        () => {
            dispatch(
                removeItemAction({
                    item: id,
                    count: String(count),
                    shouldRemoveMention: true,
                }),
            );
        },
        throttleTimeoutMs,
        undefined,
        [dispatch, id, count],
    );

    const handleFocus = React.useCallback(() => dispatch(cartSelectItemAction(id)), [dispatch, id]);

    return (
        <StyledCartListItem disabled={disabled}>
            <StyledThumb imageUrl={imageUrl}>
                <StyledIndexBadge size="s" index={index} />
            </StyledThumb>
            <StyledInfo>
                <StyledName oneLine={disabled}>{name}</StyledName>
                <StyledPrice>{price} ₽</StyledPrice>
                {disabled && <StyledLabel labelType="sold-out" />}
            </StyledInfo>
            {disabled ? (
                <Button buttonType="secondary" onFocus={handleFocus} onClick={handleRemoveClick}>
                    Удалить
                </Button>
            ) : (
                <StyledSelector>
                    <StyledSelectorButton
                        type="button"
                        aria-label="less"
                        iconType={count ? 'less' : 'remove'}
                        onFocus={handleFocus}
                        onClick={handleLessClick}
                    />
                    <StyledSelectorValue>{count}</StyledSelectorValue>
                    <StyledSelectorButton
                        type="button"
                        aria-label="more"
                        iconType="more"
                        disabled={moreButtonDisabled}
                        onFocus={handleFocus}
                        onClick={handleMoreClick}
                    />
                </StyledSelector>
            )}
        </StyledCartListItem>
    );
};
