import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { closeAppAction } from 'store/actions/app';
import { Button } from 'components/Button/Button';
import { BodyText2 } from 'components/Text/Body';
import { HeadlineText1 } from 'components/Text/Headline';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';

import figureIcon1 from './figure-1.svg';
import figureIcon2 from './figure-2.svg';
import figureIcon3 from './figure-3.svg';
import figureIcon4 from './figure-4.svg';
import figureIcon5 from './figure-5.svg';
import figureIcon6 from './figure-6.svg';
import figureIcon7 from './figure-7.svg';
import figureIcon8 from './figure-8.svg';
import figureIcon9 from './figure-9.svg';

const StatusButton = styled(Button)`
    margin-top: 64px;
`;

const StatusContainer = styled.div`
    position: relative;

    height: ${layout.contentHeightPx}px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const StatusOrderNumber = styled(BodyText2)`
    color: ${color.text.secondary};
`;

const StatusOrderTime = styled(HeadlineText1)`
    margin-top: 16px;

    text-align: center;

    color: ${color.text.primary};
`;

interface FigureProps {
    params: [string, number, number, number];
}

const StatusFigure = styled.div<FigureProps>`
    position: absolute;

    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    ${({ params: [bgUrl, topPx, leftPx, sizePx] }) => css`
        top: ${topPx}px;
        left: ${leftPx}px;

        height: ${sizePx}px;
        width: ${sizePx}px;

        background-image: url('${bgUrl}');
    `};
`;

const figureList: FigureProps['params'][] = [
    [figureIcon1, 92, 834, 61],
    [figureIcon2, 0, 482, 61],
    [figureIcon3, 280, 236, 46],
    [figureIcon4, 158, 1487, 53],
    [figureIcon5, 594, 340, 53],
    [figureIcon6, 500, 652, 60],
    [figureIcon7, 705, 1044, 40],
    [figureIcon8, 492, 1268, 40],
    [figureIcon9, 590, 1604, 90],
];

export const Status: React.FC = () => {
    const dispatch = useDispatch();
    const { orderId, deliveryTime } = useSelector(({ cart }) => cart);
    const handleButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    return (
        <StatusContainer>
            <StatusOrderNumber>Заказ #{orderId} оформлен</StatusOrderNumber>
            <StatusOrderTime>
                Спасибо за заказ!
                <br />
                Будем у вас через {deliveryTime} минут.
            </StatusOrderTime>
            <StatusButton shouldFocusOnMount onClick={handleButtonClick}>
                Очень жду
            </StatusButton>
            {figureList.map((params, index) => (
                <StatusFigure key={index} params={params} />
            ))}
        </StatusContainer>
    );
};
