import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressClickSuggestItemAction, addressSelectSuggestItemAction } from 'store/actions/addressSimplePage';
import { font } from 'style/const/font';
import { transition } from 'style/const/transition';

export const SUGGEST_ITEM_HEIGHT = 112;
export const SUGGEST_ITEM_MARGIN = 24;

const StyledRoot = styled.div`
    margin-top: ${SUGGEST_ITEM_MARGIN}px;

    display: flex;
    align-items: center;
    height: ${SUGGEST_ITEM_HEIGHT}px;
    width: 900px;
    padding: 36px 32px;
    border: 4px solid transparent;
    box-sizing: border-box;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    border-radius: 24px;
    font-weight: ${font.weight.medium};
    font-size: 32px;
    color: #fff;
    background: #1a1a1a;

    transition: border ${transition.default};

    &:focus {
        border-color: #68ea68;
    }
`;

interface SuggestListItemProps {
    index: number;
}

export interface AddressSimpleSuggestItemView {
    id: string;
    name: string;
}

export const AddressSimpleSuggestListItem: React.FC<SuggestListItemProps> = ({ index }) => {
    const { name, id } = useSelector(({ addressSimplePage: { suggests } }) => suggests[index]);
    const dispatch = useDispatch();
    const memoizedOnClick = React.useCallback(() => {
        dispatch(addressClickSuggestItemAction(id));
    }, [dispatch, id]);
    const memoizedOnFocus = React.useCallback(() => {
        dispatch(addressSelectSuggestItemAction(index));
    }, [dispatch, index]);

    return (
        <StyledRoot tabIndex={0} onClick={memoizedOnClick} onFocus={memoizedOnFocus}>
            {name}
        </StyledRoot>
    );
};
