import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { selectOverlay } from 'store/selectors';
import { Header } from 'components/Header/Header';
import { Spinner } from 'components/Spinner/Spinner';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';
import { transition } from 'style/const/transition';
import gradientBackground from 'assets/images/background-tv-sber.jpg';

const StyledContainer = styled.div`
    position: relative;

    width: 100%;
    height: 100%;

    overflow: hidden;

    background-color: ${color.background.default};
    background-image: url('${gradientBackground}');
    background-position: center;
    background-size: cover;
`;

const Layer = css`
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
`;

const AppContent = styled.div`
    z-index: 2;

    height: ${layout.contentHeightPx}px;
`;

const StyledHeader = styled(Header)`
    z-index: 2;
`;

interface OverlayProps {
    isVisible: boolean;
}

const Overlay = styled.div<OverlayProps>`
    ${Layer}
    z-index: 4;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    background-color: ${color.background.default};
    background-image: url('${gradientBackground}');
    background-position: center;
    background-size: cover;

    transition: opacity ${transition.default};

    ${({ isVisible }) =>
        !isVisible &&
        css`
            pointer-events: none;
            opacity: 0;
        `}
`;

const OverlayText = styled(BodyText1)`
    margin-top: 32px;

    color: ${color.text.primary};
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

export const AppLayout: React.FC = ({ children }) => {
    const { isVisible: isOverlayVisible, text: overlayText } = useSelector(selectOverlay);

    return (
        <StyledContainer onScroll={preventScroll}>
            <Overlay isVisible={isOverlayVisible}>
                <Spinner />
                {overlayText && <OverlayText>{overlayText}</OverlayText>}
            </Overlay>
            <StyledHeader />
            <AppContent>{children}</AppContent>
            {/* <MapBackground isVisible={isMapVisible} maskType={maskType} /> */}
        </StyledContainer>
    );
};
