const duration = {
    defaultMs: 200,
};

const timingFnEaseInOut = 'ease-in-out';

const timingFn = {
    easeInOut: timingFnEaseInOut,
    default: timingFnEaseInOut,
};

export const transition = {
    duration,
    timingFn,
    default: `${duration.defaultMs}ms ${timingFn.default}`,
};
