import React from 'react';
import styled from 'styled-components';
import { useMediaQuery } from 'beautiful-react-hooks';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressChangeInputValueAction, addressSetSuggestsAction } from 'store/actions/addressSimplePage';
import { addressSubmitAction } from 'store/actions/server';
import { getSuggests } from 'tools/2gis';
import { Input } from 'components/Input/Input';
import { Button } from 'components/Button/Button';
import { font } from 'style/const/font';

import {
    AddressSimpleSuggestListItem,
    SUGGEST_ITEM_HEIGHT,
    SUGGEST_ITEM_MARGIN,
} from './SuggestItem/AddressSimpleSuggestItem';

const StyledContainer = styled.div`
    display: flex;
    align-items: start;
    height: 100%;
    padding: 160px 360px 0;
`;

const StyledInputContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 0;
`;

const AddressSimpleButton = styled(Button)`
    margin-left: 32px;
    padding: 32px 48px;
    background: rgba(255, 255, 255, 0.12);
    border-radius: 140px;
    font-weight: ${font.weight.semibold};
    font-size: 40px;
    line-height: 48px;
    color: #ffffff;
`;

interface StyledSuggestListProps {
    hidden?: boolean;
}
const StyledSuggestList = styled.div<StyledSuggestListProps>`
    height: ${({ hidden }) => (hidden ? 0 : (SUGGEST_ITEM_HEIGHT + SUGGEST_ITEM_MARGIN) * 3)}px;
    overflow: hidden;
    transition: height 0.5s ease-in-out;
`;

interface StyledSuggestListContainerProps {
    yShift: number;
}
const StyledSuggestListContainer = styled.div<StyledSuggestListContainerProps>`
    display: flex;
    flex-direction: column;

    transition: transform 0.5s ease-in-out;
    transform: translateY(-${({ yShift }) => yShift}px);
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

const viewportHeightWithOpenedKeyboardPx = 659;

export const AddressSimple: React.FC = () => {
    const dispatch = useDispatch();
    const isKeyboardExpanded = useMediaQuery(`(max-height: ${viewportHeightWithOpenedKeyboardPx}px)`);
    const { inputValue, suggests, suggestListContainerYShift, isSubmitButtonDisabled } = useSelector(
        ({ addressSimplePage }) => addressSimplePage,
    );
    const latitude = useSelector(({ userData }) => userData.latitude);
    const longitude = useSelector(({ userData }) => userData.longitude);

    const memoizedOnInputChange = React.useCallback<(e: React.ChangeEvent<HTMLInputElement>) => void>(
        (event) => {
            dispatch(addressChangeInputValueAction(event.currentTarget.value));
        },
        [dispatch],
    );
    const memoizedOnClickButton = React.useCallback(() => {
        dispatch(addressSubmitAction());
    }, [dispatch]);

    React.useEffect(() => {
        getSuggests({ q: inputValue, latitude, longitude })
            .then((items) => {
                dispatch(addressSetSuggestsAction(items));
            })
            .catch(() => {
                dispatch(addressSetSuggestsAction([]));
            });
    }, [dispatch, inputValue, latitude, longitude]);

    return (
        <StyledContainer>
            <StyledInputContainer>
                <Input shouldFocusOnMount value={inputValue} title="Улица и дом" onChange={memoizedOnInputChange} />
                <StyledSuggestList hidden={isKeyboardExpanded} onScroll={preventScroll}>
                    <StyledSuggestListContainer yShift={suggestListContainerYShift}>
                        {suggests.map((item, index) => (
                            <AddressSimpleSuggestListItem key={item.id} index={index} />
                        ))}
                    </StyledSuggestListContainer>
                </StyledSuggestList>
            </StyledInputContainer>
            <AddressSimpleButton disabled={isSubmitButtonDisabled} onClick={memoizedOnClickButton}>
                Сохранить
            </AddressSimpleButton>
        </StyledContainer>
    );
};
