import React from 'react';
import styled from 'styled-components';
import { replace } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { closeAppAction } from 'store/actions/app';
import { Pin } from 'components/Pin/Pin';
import { HeadlineText1 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { Button } from 'components/Button/Button';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';

import faceIcon from './face.svg';
import bgImage from './bg.jpg';

const containerMarginBottomPx = 100;

const StyledContainer = styled.div`
    position: relative;
    z-index: 1;

    height: ${layout.contentHeightPx - containerMarginBottomPx}px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    z-index: 3;
`;

const faceIconSizePx = 36;

const StyledFaceIcon = styled.div`
    height: ${faceIconSizePx}px;
    width: ${faceIconSizePx}px;

    background-image: url(${faceIcon});
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
`;

const StyledTitle = styled(HeadlineText1)`
    margin-top: 52px;

    color: ${color.text.primary};
`;

const StyledSubtitle = styled(BodyText1)`
    margin-top: 12px;

    color: ${color.text.primary};
`;

const StyledButtonContainer = styled.div`
    display: flex;
    justify-content: center;
`;

const StyledButton = styled(Button)`
    margin-top: 64px;
    margin-left: 40px;
`;

const StyledBackground = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;

    background-image: url('${bgImage}');
`;

export const NoArea: React.FC = () => {
    const dispatch = useDispatch();
    const address = useSelector(({ userModel }) => userModel.address);
    const handleChangeButtonClick = React.useCallback(() => {
        dispatch(replace(ROUTES.ADDRESS_SIMPLE));
    }, [dispatch]);
    const handleCloseButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    return (
        <>
            <StyledContainer>
                <Pin>
                    <StyledFaceIcon />
                </Pin>
                <StyledTitle>{address}</StyledTitle>
                <StyledSubtitle>К сожалению, пока не доставляем по этому адресу</StyledSubtitle>
                <StyledButtonContainer>
                    <StyledButton shouldFocusOnMount onClick={handleChangeButtonClick}>
                        Изменить
                    </StyledButton>
                    <StyledButton onClick={handleCloseButtonClick} buttonType="secondary">
                        Очень жаль
                    </StyledButton>
                </StyledButtonContainer>
            </StyledContainer>
            <StyledBackground />
        </>
    );
};
