import React from 'react';
import { addressFullValidationOkThunk } from 'store/thunks/addressFullPage';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullPhoneNumberInputChangeAction } from 'store/actions/addressFullPage';
import { Input } from 'components/Input/Input';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullPhone = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.phoneNumberInputValue);
    const handleInputChange = React.useCallback(
        ({ nativeEvent }: React.ChangeEvent<HTMLInputElement>) => {
            // без этого тайпгварда TS не хочет показывать правильные поля
            if (nativeEvent instanceof InputEvent) {
                const { inputType, data } = nativeEvent;

                dispatch(addressFullPhoneNumberInputChangeAction({ inputType, data }));
            }
        },
        [dispatch],
    );
    const handleInputSubmit = React.useCallback(() => {
        dispatch(addressFullValidationOkThunk());
    }, [dispatch]);

    return (
        <StyledInput
            title="Номер телефона"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
        />
    );
};
