import { AppState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { formatPhoneNumber } from 'tools/format-phone-number';

const computeNextFocus = (state: AppState) => {
    const { phoneNumber, apartment, entrance, floor } = state.userModel;

    const options = {
        [ROUTES.ADDRESS_FULL_PHONE]: phoneNumber,
        [ROUTES.ADDRESS_FULL_APARTMENT]: apartment,
        [ROUTES.ADDRESS_FULL_ENTRANCE]: entrance,
        [ROUTES.ADDRESS_FULL_FLOOR]: floor,
    };

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const result = Object.entries(options).find(([_, value]) => !value);

    return result ? result[0] : ROUTES.ADDRESS_FULL_ALL;
};

export const addressFullPageView: AppReducer = (state) => {
    state.addressFullPage.nextFocus = computeNextFocus(state);
    // так как здесь этот инпут заблочен, показываем данные с уровня data
    state.addressFullPage.addressInputValue = state.userData.address;
    state.addressFullPage.phoneNumberInputValue = formatPhoneNumber(state.userModel.phoneNumber);
    state.addressFullPage.apartmentInputValue = state.userModel.apartment;
    state.addressFullPage.entranceInputValue = state.userModel.entrance;
    state.addressFullPage.floorInputValue = state.userModel.floor;
    state.addressFullPage.isSubmitButtonDisabled =
        !state.userModel.phoneNumber || state.userModel.phoneNumber.length !== 12;
};
